




































import { getTld, isProdEnvUrl } from "@/utils/common";
import { Component, Vue, Watch } from 'vue-property-decorator'
@Component({
  components: {}
})
export default class ViewComponent extends Vue {
  /**data */
  tld = '' // 一级域名 top-level domain
  prefixHerf = '' // 域名前缀

  /**life hook */
  created(){
    // console.log('isProdEnvUrl()：',isProdEnvUrl())
    if(isProdEnvUrl()){
      this.tld = getTld(location.hostname)
    }

    // 如果是github需要补全herf路径,因为仅是页面server,不支持相对路径路由
    if(getTld(location.hostname) === 'github.io'){
      this.prefixHerf = location.href.slice(0,location.href.lastIndexOf('/'))
    }
  }
}
